<template>
  <div class="d-flex flex-column min-vh-100">
    <!-- Header -->
    <header class="bg-dark text-white py-4 shadow-sm">
      <div class="container d-flex justify-content-between align-items-center">
        <div class="d-flex align-items-center gap-3">
          <a href="/">
              <img src="../../public/img/bookstore.png" alt="Logo" height="60" />
          </a>
          <h2 class="m-0">Proyecto 2</h2>
        </div>
        <router-link to="/" class="btn btn-outline-light">← Volver</router-link>
      </div>
    </header>

    <!-- Contenido principal -->
    <main class="container my-5 flex-grow-1">
      <div v-if="book" class="card p-4 shadow">
        <div class="row g-3">
          <div class="col-md-4">
            <img
              v-if="book.imagen"
              :src="getImageUrl(book.imagen)"
              class="card-img-top bg-white p-3 rounded-top"
              :alt="book.nombre"
              style="height: 450px; object-fit: contain;"
            />
          </div>
          <div class="col-md-8">
            <h2 class="text-danger">{{ book.nombre }}</h2>
            <p><strong>Autor:</strong> {{ book.autor }}</p>
            <p><strong>Precio:</strong> {{ book.precio }} €</p>
            <p><strong>Editorial:</strong> {{ book.editorial }}</p>
            <p><strong>Páginas:</strong> {{ book.paginas }}</p>
            <p><strong>Año:</strong> {{ book.anio }}</p>
            <p><strong>Fecha de subida:</strong> {{ book.fecha_subida }}</p>
            <p><strong>Código:</strong> {{ book.codigo }}</p>
            <p><strong>Descripción:</strong> {{ book.descripcion }}</p>

            <div class="mt-4 d-flex gap-3">
              <button class="btn btn-warning" @click="openEditModal">Editar</button>
              <button class="btn btn-danger" @click="deleteBook">Eliminar</button>
            </div>
          </div>
        </div>
      </div>

      <div v-else class="alert alert-warning">Cargando libro...</div>
    </main>

    <!-- Footer -->
    <footer class="bg-dark text-white py-3 mt-auto">
      <div class="container text-center">
        <small>© 2025 Proyecto 2 - Elias Arias - Todos los derechos reservados.</small>
      </div>
    </footer>

    <!-- Modal de edición -->
    <div class="modal fade" id="editModal" tabindex="-1" aria-hidden="true">
      <div class="modal-dialog modal-lg">
        <div class="modal-content shadow">
          <div class="modal-header bg-secondary text-white">
            <h5 class="modal-title">Editar Libro</h5>
            <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal"></button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitEdit">
              <div class="row g-3">
                <div class="col-md-6">
                  <label class="form-label">Nombre</label>
                  <input type="text" class="form-control" v-model="editForm.nombre" required />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Autor</label>
                  <input type="text" class="form-control" v-model="editForm.autor" required />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Precio</label>
                  <input type="number" class="form-control" v-model="editForm.precio" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Editorial</label>
                  <input type="text" class="form-control" v-model="editForm.editorial" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Páginas</label>
                  <input type="number" class="form-control" v-model="editForm.paginas" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Año</label>
                  <input type="number" class="form-control" v-model="editForm.anio" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Fecha de Subida</label>
                  <input type="date" class="form-control" v-model="editForm.fecha_subida" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">Código</label>
                  <input type="text" class="form-control" v-model="editForm.codigo" required />
                </div>
                <div class="col-12">
                  <label class="form-label">Descripción</label>
                  <textarea class="form-control" v-model="editForm.descripcion" rows="3"></textarea>
                </div>
              </div>
              <div class="mt-4 d-flex justify-content-end">
                <button type="submit" class="btn btn-success px-4">Guardar Cambios</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      book: null,
      editForm: {}
    }
  },
  mounted() {
    const id = this.$route.params.id
    fetch(`http://localhost:8000/index.php/api/books/${id}`)
      .then(res => res.json())
      .then(data => {
        if (data.error) {
          alert('Libro no encontrado');
        } else {
          this.book = data;
        }
      })
      .catch(err => {
        console.error('Error al cargar el libro:', err);
        alert('Ocurrió un error al cargar el libro');
      });
  },
  methods: {
    getImageUrl(imagen) {
      if (!imagen) return ''
      return imagen.startsWith('http')
        ? imagen
        : `http://localhost:8000/${imagen}`
    },
    openEditModal() {
      this.editForm = { ...this.book }
      const modalElement = document.getElementById('editModal')
      const modalInstance = new bootstrap.Modal(modalElement)
      modalInstance.show()
    },
    submitEdit() {
      const formData = new FormData()
      for (const key in this.editForm) {
        formData.append(key, this.editForm[key])
      }

      fetch(`http://localhost:8000/index.php/api/books/edit`, {
        method: 'POST',
        body: formData
      })
        .then(res => res.json())
        .then(data => {
          alert('Libro actualizado con éxito')
          window.location.reload()
        })
        .catch(err => {
          console.error('Error al actualizar:', err)
          alert('Error al guardar cambios')
        })
    },
    deleteBook() {
      if (confirm("¿Estás seguro de que deseas eliminar este libro?")) {
        fetch(`http://localhost:8000/index.php/api/books/${this.book.id}`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
          body: '_method=DELETE'
        })
          .then(res => {
            if (!res.ok) throw new Error('Error al eliminar');
            return res.json();
          })
          .then(() => {
            alert('Libro eliminado');
            this.$router.push('/');
          })
          .catch(err => {
            console.error('Error al eliminar el libro:', err);
            alert('No se pudo eliminar el libro');
          });
      }
    }
  }
}
</script>
