<template>
  <div class="d-flex flex-column min-vh-100">
    <!-- Header -->
    <header class="bg-dark text-white py-4 shadow-sm">
      <div class="container d-flex justify-content-between align-items-center">
        <div class="d-flex align-items-center gap-3">
          <img src="../../public/img/bookstore.png" alt="Logo" height="60" />
          <h2 class="m-0">Proyecto 2</h2>
        </div>
        <button class="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#agregarModal"> Agregar Nuevo</button>
      </div>
    </header>

    <!-- Contenido principal -->
    <main class="flex-grow-1">
      <!-- Filtros -->
      <div class="container my-4">
        <div class="row g-3">
          <div class="col-md-6">
            <input type="text" class="form-control" placeholder="Buscar libro..." v-model="searchTerm" />
          </div>
          <div class="col-md-6">
            <select class="form-select" v-model="selectedCategory">
              <option value="">Todas las categorías</option>
              <option v-for="cat in categorias" :key="cat.id" :value="cat.id">{{ cat.nombre }}</option>
            </select>
          </div>
        </div>
      </div>

     <!-- Lista de Libros -->
<div class="container">
  <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 g-4">
    <div v-for="book in filteredBooks" :key="book.id" class="col">
      <div class="card h-100 shadow-sm border-0 rounded-4">
        <img
          v-if="book.imagen"
          :src="getImageUrl(book.imagen)"
          class="card-img-top bg-white rounded-top"
          :alt="book.nombre"
          style="height: 300px; object-fit:fill;"
        />
        <div class="card-body">
          <h5 class="card-title text-primary fw-bold text-truncate" :title="book.nombre">{{ book.nombre }}</h5>
          <p class="mb-1 text-muted"><strong>Autor:</strong> {{ book.autor }}</p>
          <p class="text-success fw-semibold">{{ book.precio }} €</p>
        </div>
        <div class="card-footer bg-white border-0 d-flex justify-content-center">
          <router-link
            :to="`/books/${book.id}`"
            class="btn btn-outline-primary btn-sm w-100"
          >Ver Detalles</router-link>
        </div>
      </div>
    </div>
  </div>
</div>



    </main>

    <!-- Footer -->
    <footer class="bg-dark text-white py-3 mt-auto">
      <div class="container text-center">
        <small>© 2025 Proyecto 2 - Elias Arias - Todos los derechos reservados.</small>
      </div>
    </footer>

    <!-- Modal de Agregar Libro -->
    <div class="modal fade" id="agregarModal" tabindex="-1" aria-hidden="true">
      <div class="modal-dialog modal-lg">
        <div class="modal-content shadow">
          <div class="modal-header bg-secondary text-white">
            <h5 class="modal-title">Agregar Libro</h5>
            <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal"></button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="handleSubmit">
              <div class="row g-3">
                <div class="col-md-6">
                  <label class="form-label">Imagen</label>
                  <input type="file" class="form-control" @change="handleFileUpload"  required/>
                </div>
                <div v-for="(field, i) in fields" :key="i" class="col-md-6">
                  <label class="form-label">{{ field.label }}</label>
                  <input
                    v-if="field.type !== 'textarea'"
                    :type="field.type"
                    class="form-control"
                    v-model="form[field.model]"
                    :required="field.required"
                  />
                  <textarea
                    v-if="field.type === 'textarea'"
                    class="form-control"
                    rows="3"
                    v-model="form[field.model]"
                  ></textarea>
                </div>
                <div class="col-12">
                  <label class="form-label">Categorías</label>
                  <select class="form-select" v-model="form.categorias" multiple>
                    <option v-for="cat in categorias" :key="cat.id" :value="cat.id">{{ cat.nombre }}</option>
                  </select>
                </div>
              </div>
              <div class="mt-4 d-flex justify-content-end">
                <button type="submit" class="btn btn-success px-4">Guardar</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>


<script>
export default {
  data() {
    return {
      books: [],
      categorias: [],
      selectedCategory: '',
      searchTerm: '',
      form: {
        imagen: null,
        nombre: '',
        autor: '',
        precio: '',
        fecha_subida: '',
        editorial: '',
        paginas: '',
        anio: '',
        codigo: '',
        descripcion: '',
        categorias: []
      },
    fields: [
        { label: 'Nombre', model: 'nombre', type: 'text', required: true },
        { label: 'Autor', model: 'autor', type: 'text', required: true },
        { label: 'Precio', model: 'precio', type: 'number', required: true },
        { label: 'Fecha Subida', model: 'fecha_subida', type: 'date', required: true },
        { label: 'Editorial', model: 'editorial', type: 'text', required: true },
        { label: 'Páginas', model: 'paginas', type: 'number', required: true },
        { label: 'Año', model: 'anio', type: 'number', required: true },
        { label: 'Código', model: 'codigo', type: 'text', required: true },
        { label: 'Descripción', model: 'descripcion', type: 'textarea', required: true }
      ]
    }
  },
  computed: {
    filteredBooks() {
      return this.books.filter(book => {
        const matchesSearch = book.nombre?.toLowerCase().includes(this.searchTerm.toLowerCase())
        const matchesCategory = this.selectedCategory === '' || book.categorias?.some(c => c.id == this.selectedCategory)
        return matchesSearch && matchesCategory
      })
    }
  },
  mounted() {
    this.fetchBooks()
    this.fetchCategories()
  },
  methods: {
    fetchBooks() {
      fetch('http://localhost:8000/index.php/api/books')
        .then(res => res.json())
        .then(data => {
          this.books = Array.isArray(data) ? data : []
        })
    },
    fetchCategories() {
      fetch('http://localhost:8000/index.php/api/categories')
        .then(res => res.json())
        .then(data => {
          this.categorias = Array.isArray(data) ? data : []
        })
    },
    handleFileUpload(e) {
      this.form.imagen = e.target.files[0]
    },
     getImageUrl(imagen) {
      if (!imagen) return ''
      return imagen.startsWith('http')
        ? imagen
        : `http://localhost:8000/${imagen}`  // ajusta si tu ruta es diferente
    },
    handleSubmit() {
      const formData = new FormData()
      for (const key in this.form) {
        if (key === 'categorias') {
          this.form[key].forEach(id => formData.append('categorias[]', id))
        } else {
          formData.append(key, this.form[key])
        }
      }

      fetch('http://localhost:8000/index.php/api/books', {
        method: 'POST',
        body: formData
      }).then(() => {
        this.fetchBooks()
        const modal = bootstrap.Modal.getInstance(document.getElementById('agregarModal'))
        modal?.hide()
      })
    }
  }
}
</script>

<style scoped>
.card-title {
  font-size: 1rem;
  transition: color 0.3s ease;
}

.card-img-top {
  transition: transform 0.4s ease-in-out;
}

.card {
  transition: box-shadow 0.4s ease, transform 0.4s ease, background-color 0.4s ease;
  border-radius: 12px;
  overflow: hidden;
}

.card:hover {
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px) scale(1.02);
  background-color: #f8f9fa;
}

.card:hover .card-img-top {
  transform: scale(1.05);
}

.card:hover .card-title {
  color: #0d6efd;
}
</style>

